* {
  font-family: Helvetica, Arial, sans-serif;
}  

.category-title {
  font-size: 50px;
  display: inline-block;
  
}


body {
  padding: 60px;

}

input {
  background: #ffff;
}

 .card {
  align-content: center;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
